<template>
  <t-table
    class="list-wrapper"
    :isLoading="isLoading"
    :data="data"
    :openFilter="true"
    :pagination='pagination'
    :sizeChange="handleSizeChange"
    :currentChange="handleCurrentChange"
    :row-class-name='setRowClass'
  >
    <template #filter>
      <el-button class="mgr-24" type="primary" size="small" @click='handleCreateArticle'>
        
      </el-button>
      <t-select class="select-w-120" v-if='tagsEnum.length' :data='tagsEnum' v-model='params.tag' @change='handleTagChange' />
      <span class="space"></span>
      <el-input class="input-w-300" size="small" type='text' v-model='params.keyword' @keydown.enter.native="handleSearch" @input.native='handleInput'>
        <el-button size="small" type="primary" slot="append" icon="el-icon-search" @click='handleSearch'></el-button>
      </el-input>
    </template>
    <template #table>
      <el-table-column prop="title" label="" align="center" />
      <el-table-column label="" align="center" width='96'>
        <template slot-scope="scope">
          <span>{{ scope.row.isPublish ? '' : '' }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" width='320'>
        <template slot-scope="scope">
          <!--  -->
          <span v-for="(tag, index) in scope.row.tags.split(',')" :key="tag">
            <el-tag class="table-tags" :type='tagTypes[index]'>{{ tag }}</el-tag>
          </span>

        </template>
      </el-table-column>
      <el-table-column label="" prop='createdTime' align="center" width='240'>
        <template slot-scope="scope">
          <span>{{ scope.row.createdTime | t-time-format }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" width="120">
          <template slot-scope="scope">
            <t-dropdown trigger='click' class="list-dropdown" size='small' :dropdownItemEnum='dropdownItemEnum(scope.row)' />
          </template>
        </el-table-column>
    </template>
  </t-table>
</template>

<script>
import { checkArticleList, deleteArticle, setTopArticleListItem, cancelSetTopArticleListItem, getArticleTagsEnum } from '@/apis/apis'
import { mapMutations } from 'vuex'
export default {
  name: 'articleList',
  components: {},
  props: {},
  data () {
    return {
      isLoading: false,
      tagsEnum: [],
      data: [],
      pagination: {},
      params: {
        page: 1,
        size: 20,
        keyword: null,
        tag: null
      }
    }
  },
  computed: {
    tagTypes () {
      return ['success', 'info', 'warning', 'dander']
    }
  },
  methods: {
    ...mapMutations(['SETGLOBALMASK']),
    dropdownItemEnum ({ _id, title, isTop }) {
      return [
        {
          label: '',
          attrs: {
            command: 'delete'
          },
          fnCallback: (command) => {
            this.handleDelete(_id)
          }
        },
        {
          label: '',
          attrs: {
            command: 'edit'
          },
          fnCallback: (command) => {
            this.handleEdit(_id)
          }
        },
        {
          label: '',
          attrs: {
            command: 'check'
          },
          fnCallback: (command) => {
            this.handleCheck(_id, title)
          }
        },
        {
          label: isTop ? '' : '',
          attrs: {
            command: 'setTop'
          },
          fnCallback: (command) => {
            isTop ? this.cancelSetTop(_id) : this.handleSetTop(_id)
          }
        }
      ]
    },
    getArticleTagsEnum () {
      return new Promise((resolve) => {
        getArticleTagsEnum()
          .then((response) => {
            const { code, result } = response.data
            if (code === 0) {
              result.map((i) => {
                this.tagsEnum.push({ label: i, value: i })
              })
            }
          })
      })
    },
    // 
    getList () {
      this.isLoading = true
      return new Promise((resolve) => {
        checkArticleList(this.params).then(response => {
          setTimeout(() => {
            const { result, code } = response.data
            if (code === 0) {
              const { list, total } = result
              this.data = list
              this.pagination.total = total
              this.pagination.page = this.params.page
              this.pagination.size = this.params.size
              this.isLoading = false
              resolve()
            }
          }, this.$store.state.apiDelay)
        })
      })
    },
    // 
    deleteArticle (id) {
      this.SETGLOBALMASK(true)
      deleteArticle({ id })
        .then((response) => {
          setTimeout(() => {
            const { code, msg } = response.data
            if (code === 0) {
              this.SETGLOBALMASK(false)
              this.$message.success(msg)
              this.getList()
            }
          }, this.$store.state.apiDelay)
        })
    },
    setTopArticleListItem (params) {
      return new Promise((resolve) => {
        setTopArticleListItem(params)
          .then((response) => {
            const { code } = response.data
            if (code === 0) {
              this.getList()
                .then(() => {
                  resolve()
                })
            }
          })
      })
    },
    cancelSetTopArticleListItem (params) {
      return new Promise((resolve) => {
        cancelSetTopArticleListItem(params)
          .then((response) => {
            const { code } = response.data
            if (code === 0) {
              this.getList()
                .then(() => {
                  resolve()
                })
            }
          })
      })
    },
    //  size 
    handleSizeChange (val) {
      this.params.size = val
      /**
       *  size  page 
       *  page 
       *  page 
       *
       *  ElementUI  size  page  size  page 
       *  this.params.page this.params.size 
       */
      if (
        this.pagination.total &&
        Math.ceil(this.pagination.total / val) >= this.params.page
      ) {
        this.getList()
      }
    },
    //  page 
    handleCurrentChange (val) {
      this.params.page = val
      this.getList()
    },
    // 
    handleCreateArticle () {
      this.$router.push({
        path: '/article-operation'
      })
    },
    // 
    handleEdit (id) {
      this.$router.push({
        path: '/article-operation',
        query: {
          mode: 'eidt',
          id
        }
      })
    },
    // 
    handleDelete (id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.deleteArticle(id)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    handleCheck (_id, title) {
      window.sessionStorage.setItem('ARTICLETITLE', title)
      this.$router.push({
        path: '/check-article',
        query: {
          id: _id
        }
      })
    },
    handleSetTop (id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.setTopArticleListItem({ id })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    cancelSetTop (id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.cancelSetTopArticleListItem({ id })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    // 
    handleSearch () {
      this.params.tag = null
      this.getList()
    },
    //  input  params.role
    handleInput () {
      this.params.tag = null
    },
    handleTagChange (val) {
      this.params.keyword = null
      this.getList()
    },
    // role 
    selectHandleChange () {
      this.params.keyword = null
      this.getList()
    },
    setRowClass ({ row }) {
      const { isTop } = row
      if (isTop) {
        return 'is-set-top'
      }
    }
  },
  created () {
    // 
    this.getList()
    this.getArticleTagsEnum()
  },
  mounted () {},
  watch: {}
}
</script>

<style lang="scss" scoped>
@import '@/assets/styles/scss/mixin.scss';
.list-wrapper {
  width: 100%;
  height: 100%;
  .space {
    flex: 1;
  }
  .table-tags {
    margin: 0px 5px;
  }
  .list-dropdown {
    cursor: pointer;
    width: 98px;
  }
  /deep/tr.is-set-top {
    @include gradient(rgba(0,0,0,.2), #fff);
  }
}
</style>
