import Vue from 'vue'
import { Message } from 'element-ui'
import { debounce } from './directives'

Vue.directive('t-debounce', {
  inserted: function (el, binding) {
    const { value } = binding
    /*  */
    const {
      type = 'onclick',
      fn = function () {
        Message.error(' fn ')
      },
      params = []
    } = value
    /*  */
    el[type] = debounce(fn, 1000, ...params)
  }
})
