<!--
   markdown 
   render  -> markdown-body render 
-->
<template>
  <mavon-editor
    class="mavon-editor-wrapper"
    :toolbars="toolbars"
    :imageFilter="imageFilter"
    codeStyle="atom-one-dark"
    ref="mavonEditor"
    @imgAdd="imgAdd"
    v-on="$listeners"
    v-bind="$attrs"
    v-model="editorContent"
  />
</template>

<script>
import axios from 'axios'
import { mapMutations } from 'vuex'
export default {
  name: 't-mavon-editor',
  components: {},
  props: {
    action: {
      type: String,
      default: '../api/upload'
    },
    defaultContent: {
      type: String,
      default: ''
    },
    size: {
      type: Number,
      default: 1024
    },
    // 
    directory: {
      type: String,
      default: 'article'
    }
  },
  data () {
    return {
      editorContent: ''
    }
  },
  computed: {
    toolbars () {
      return {
        bold: true, // 
        italic: true, // 
        header: true, // 
        underline: true, // 
        strikethrough: true, // 
        mark: true, // 
        superscript: true, // 
        subscript: true, // 
        quote: true, // 
        ol: true, // 
        ul: true, // 
        link: true, // 
        imagelink: true, // 
        code: true, // code
        table: true, // 
        fullscreen: true, // 
        readmodel: false, // 
        htmlcode: true, // html
        help: true, // 
        /* 1.3.5 */
        undo: true, // 
        redo: true, // 
        trash: true, // 
        save: true, // eventssave
        /* 1.4.2 */
        navigation: false, // 
        /* 2.1.8 */
        alignleft: true, // 
        aligncenter: true, // 
        alignright: true, // 
        /* 2.2.1 */
        subfield: false, // 
        preview: true // 
      }
    }
  },
  methods: {
    ...mapMutations(['SETGLOBALMASK']),
    // 
    imgAdd (pos, file) {
      if (!this.$listeners.imgAdd) {
        this.SETGLOBALMASK(true)
        const formData = new FormData()
        formData.append('file', file)
        formData.append('directory', this.directory)
        axios
          .post(this.action, formData, {
            headers: {
              'Content-type': 'multipart/form-data'
            }
          })
          .then(res => {
            const { url } = res.data.result
            this.$refs.mavonEditor.$img2Url(pos, url)
            this.SETGLOBALMASK(false)
            this.$emit('imgAddCallBack', pos, url)
          })
      }
    },
    // 
    imageFilter (file) {
      if (!this.$attrs.imageFilter) {
        const { name, size } = file
        const isOverSize = size > this.size * 1024
        if (isOverSize) {
          this.$message.error(`${name}${this.size}kb`)
        }
        return !isOverSize
      }
    }
  },
  created () {
    // 
    this.editorContent = this.defaultContent
  }
}
</script>

<style lang="scss" scoped>
.mavon-editor-wrapper {
  width: 100%;
  height: 100%;
  overflow: auto;
  /deep/.v-show-content {
    img {
      min-width: 200px;
      max-width: 400px;
      margin: 0;
    }
  }
}
</style>

