<template>
  <t-dialog
    :dialogCancel="dialogClose"
    :dialogClose="dialogClose"
    :visible="visible"
    :title="dialogTitle"
    :showFooter='true'
    @open='handleOpen'
  >
    <template v-slot:body>
      <el-form
        ref="createPluginItem"
        size="small"
        :model="model"
        :rules="rules"
        label-width="96px"
        label-position="left"
      >
        <div class="form-body">
          <el-form-item label="" prop="name">
            <el-input v-model="model.name"></el-input>
          </el-form-item>
          <el-form-item label="demo " prop="url">
            <el-input v-model="model.url"></el-input>
          </el-form-item>
          <el-form-item label="demo " prop="desc">
            <el-input type="textarea" v-model="model.desc"></el-input>
          </el-form-item>
        </div>
      </el-form>
    </template>
    <template v-slot:footer class="form-footer">
      <el-button type="info" size='small' @click='dialogClose'> </el-button>
      <el-button type="primary" size='small' @click='submitForm'> </el-button>
    </template>
  </t-dialog>
</template>

<script>
import { createPluginListItem, checkPluginListItem, updatePluginListItem } from '@/apis/apis'
export default {
  name: 'OperationDialog',
  components: {},
  props: {
    visible: {
      type: Boolean,
      default: false
    },
    //  id 
    id: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      model: {
        name: '',
        url: '',
        desc: ''
      },
      rules: {
        name: [{ required: true, message: '', trigger: 'blur' }],
        url: [{ required: true, message: 'demo', trigger: 'blur' }],
        desc: [{ required: true, message: 'demo', trigger: 'blur' }]
      }
    }
  },
  computed: {
    dialogTitle () {
      return this.id ? '' : ''
    }
  },
  methods: {
    handleOpen () {
      this.model = {
        name: '',
        desc: '',
        url: ''
      }
      this.id &&
      this.checkPluginListItem()
    },
    // 
    createPluginListItem () {
      return new Promise((resolve) => {
        createPluginListItem(this.model)
          .then((response) => {
            const { code } = response.data
            if (code === 0) {
              resolve()
            }
          })
      })
    },
    // 
    checkPluginListItem () {
      return new Promise((resolve) => {
        checkPluginListItem({ id: this.id })
          .then((response) => {
            const { code, result } = response.data
            if (code === 0) {
              this.model = result
              resolve()
            }
          })
      })
    },
    // 
    updatePluginListItem () {
      return new Promise((resolve) => {
        const params = Object.assign(this.model, { id: this.id })
        updatePluginListItem(params)
          .then((response) => {
            const { code } = response.data
            if (code === 0) {
              resolve()
            }
          })
      })
    },
    // 
    submitForm () {
      this.$nextTick(() => {
        this.$refs.createPluginItem.validate(valid => {
          if (valid) {
            this.id
              ? this.updatePluginListItem()
                .then(() => {
                  this.$emit('dialogClose', true)
                })
              : this.createPluginListItem()
                .then(() => {
                  this.$emit('dialogClose', true)
                })
          } else {
            console.log('error submit!!')
            return false
          }
        })
      })
    },
    dialogClose () {
      this.model = {
        name: '',
        desc: '',
        url: ''
      }
      this.$refs.createPluginItem.resetFields()
      this.$emit('dialogClose')
    }
  },
  created () {},
  mounted () {},
  watch: {}
}
</script>

