import './clipboard'
import './fullScreen'
import './throttle'
import './debounce'
import './permission'

/**
 * 
 * @param {} fn
 * @param {} delay
 *  arguments 
 */

export function throttle (fn, delay) {
  /*  */
  const params = [...arguments].splice(2)
  /*  0 */
  let clickTime = 0
  return function () {
    /*  */
    const now = Date.now()
    /*  -  >   */
    if (now - clickTime > delay) {
      /*  arguments  */
      fn.call(this, ...params)
      /*  */
      clickTime = now
    }
  }
}

/**
 * 
 * @param {} fn
 * @param {} delay
 */
export function debounce (fn, delay) {
  /*  */
  const params = [...arguments].splice(2)
  var timer = null
  return function () {
    /*  */
    clearTimeout(timer)
    /*  */
    timer = setTimeout(function () {
      fn.call(this, ...params)
    }, delay)
  }
}
