import Vue from 'vue'
import { Message } from 'element-ui'
import { throttle } from './directives'

function click (el, value) {
  // 
  el.onclick = throttle(() => {
    if (!value) {
      // Message.error(i18n.t('directives.clipboard.'))
      Message.error('')
      return false
    }

    const inputDom = document.createElement('input')
    inputDom.value = value
    document.body.appendChild(inputDom)
    inputDom.select()
    document.execCommand('Copy')
    // Message.success(i18n.t('directives.clipboard.'))
    Message.success('')
    inputDom.remove()
  }, 1000)
}

Vue.directive('t-clipboard', {
  inserted (el, binding) {
    const { value = null } = binding
    click(el, value)
  },
  update (el, binding) {
    const { value = null } = binding
    click(el, value)
  }
})
