<template>
  <div class="plugin-list-wrapper">
    <t-table
      :isLoading="isLoading"
      :data="data"
      openFilter
      :pagination="pagination"
      :sizeChange="handleSizeChange"
      :currentChange="handleCurrentChange"
      :row-class-name='setRowClass'
    >
      <template #filter>
        <el-button
          class="mgr-24"
          type="primary"
          size="small"
          @click="handleCreate"
        >
          
        </el-button>
        <span class="space"></span>
        <el-input class="input-w-300" size="small" type='text' v-model='params.keyword' @keydown.enter.native="handleSearch">
          <el-button size="small" type="primary" slot="append" icon="el-icon-search" @click='handleSearch'></el-button>
        </el-input>
      </template>
      <template #table>
        <el-table-column label="" align="center" width="60">
          <template slot-scope="scope">
            <span>{{ scope.$index + 1 }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="name" label="" align="center" width='180' />
        <el-table-column prop="url" label="demo " align="center" width='180' />
        <el-table-column label="" align="center" width="250">
          <template slot-scope="scope">
            <span>{{ scope.row.createdTime |  t-time-format }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="desc" label="demo " align="center" />
        <el-table-column label="" align="center" width="120">
          <template slot-scope="scope">
            <t-dropdown trigger='click' class="list-dropdown" size='small' :dropdownItemEnum='dropdownItemEnum(scope.row)' />
          </template>
        </el-table-column>
      </template>
    </t-table>
    <OperationDialog @dialogClose='dialogClose' :visible='visible' :id='id' />
  </div>
</template>

<script>
import { checkPluginList, deletePluginListItem, setTopPluginListItem, cancelSetTopPluginListItem } from '@/apis/apis'
import OperationDialog from '../components/operationDialog'
export default {
  name: 'PluginList',
  components: {
    OperationDialog
  },
  props: {},
  data () {
    return {
      id: '',
      isLoading: false,
      data: [],
      params: {
        page: 1,
        size: 20,
        keyword: null
      },
      pagination: {},
      visible: false
    }
  },
  computed: {},
  methods: {
    dropdownItemEnum ({ _id, url, isTop }) {
      return [
        {
          label: '',
          attrs: {
            command: 'delete'
          },
          fnCallback: (command) => {
            this.handleDelete(_id)
          }
        },
        {
          label: '',
          attrs: {
            command: 'edit'
          },
          fnCallback: (command) => {
            this.handleEdit(_id)
          }
        },
        {
          label: '',
          attrs: {
            command: 'check'
          },
          fnCallback: (command) => {
            this.handleCheck(url)
          }
        },
        {
          label: isTop ? '' : '',
          attrs: {
            command: 'setTop'
          },
          fnCallback: (command) => {
            isTop ? this.cancelSetTop(_id) : this.handleSetTop(_id)
          }
        }
      ]
    },
    getList () {
      this.isLoading = true
      return new Promise((resolve) => {
        checkPluginList(this.params).then(response => {
          setTimeout(() => {
            const { result, code } = response.data
            if (code === 0) {
              const { list, total } = result
              this.data = list
              this.pagination.total = total
              this.pagination.page = this.params.page
              this.pagination.size = this.params.size
              this.isLoading = false
              resolve()
            }
          }, this.$store.state.apiDelay)
        })
      })
    },
    deletePluginListItem (params) {
      return new Promise((resolve) => {
        deletePluginListItem(params)
          .then((response) => {
            const { code } = response.data
            if (code === 0) {
              this.getList()
                .then(() => {
                  resolve()
                })
            }
          })
      })
    },
    setTopPluginListItem (params) {
      return new Promise((resolve) => {
        setTopPluginListItem(params)
          .then((response) => {
            const { code } = response.data
            if (code === 0) {
              this.getList()
                .then(() => {
                  resolve()
                })
            }
          })
      })
    },
    cancelSetTopPluginListItem (params) {
      return new Promise((resolve) => {
        cancelSetTopPluginListItem(params)
          .then((response) => {
            const { code } = response.data
            if (code === 0) {
              this.getList()
                .then(() => {
                  resolve()
                })
            }
          })
      })
    },
    //  size 
    handleSizeChange (val) {
      this.params.size = val
      /**
       *  size  page 
       *  page 
       *  page 
       *
       *  ElementUI  size  page  size  page 
       *  this.params.page this.params.size 
       */
      if (
        this.pagination.total &&
        Math.ceil(this.pagination.total / val) >= this.params.page
      ) {
        this.getList()
      }
    },
    //  page 
    handleCurrentChange (val) {
      this.params.page = val
      this.getList()
    },
    dialogClose (boolean = false) {
      this.visible = false
      boolean && this.getList()
    },
    handleCreate () {
      this.id = ''
      this.visible = true
    },
    handleDelete (id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.deletePluginListItem({ id })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    handleEdit (id) {
      this.id = id
      this.visible = true
    },
    handleCheck (url) {
      this.$router.push(url)
    },
    handleSetTop (id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.setTopPluginListItem({ id })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    cancelSetTop (id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.cancelSetTopPluginListItem({ id })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    // 
    handleSearch () {
      this.getList()
    },
    setRowClass ({ row }) {
      const { isTop } = row
      if (isTop) {
        return 'is-set-top'
      }
    }
  },
  created () {
    this.getList()
  },
  mounted () {},
  watch: {}
}
</script>

<style lang="scss" scoped>
@import '@/assets/styles/scss/mixin.scss';
.plugin-list-wrapper {
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  .space {
    flex: 1;
  }
  .list-dropdown {
    cursor: pointer;
    width: 98px;
  }
  /deep/tr.is-set-top {
    @include gradient(rgba(0,0,0,.2), #fff);
  }
}
</style>
