<template>
  <t-table
    class="list-wrapper"
    :isLoading="isLoading"
    :data="data"
    :openFilter="true"
    :pagination='pagination'
    :sizeChange="handleSizeChange"
    :currentChange="handleCurrentChange"
  >
    <template #filter>
      <el-button class="mgr-24" type="primary" size="small" @click='handleCreateUser' v-t-permission="'USERSLISTADD'">
        
      </el-button>
      <t-select class="select-w-120" :data='roleEnum' v-model='params.role' @change='selectHandleChange' />
      <span class="space"></span>
      <el-input class="input-w-300" size="small" type='text' v-model='params.keyword' @keydown.enter.native="handleSearch" @input.native='handleInput'>
        <el-button size="small" type="primary" slot="append" icon="el-icon-search" @click='handleSearch'></el-button>
      </el-input>
    </template>
    <template #table>
      <el-table-column prop="name" label="" align="center" />
      <el-table-column prop="account" label="" align="center" />
      <el-table-column prop="mobile" label="" align="center" />
      <el-table-column prop="roleDesc" label="" align="center" />
      <el-table-column label="" align="center" width="200">
        <template slot-scope="scope">
          <el-button type="text" size="small" @click='handleDelete(scope.row._id)'></el-button>
          <el-button type="text" size="small" @click='handleEdit(scope.row._id)'></el-button>
        </template>
      </el-table-column>
    </template>
  </t-table>
</template>

<script>
import { roleEnum } from '@/assets/js/contants/enum'
import { checkAllUsers, deleteUser } from '@/apis/apis'
import { mapMutations } from 'vuex'
export default {
  name: 'permissionList',
  components: {},
  props: {},
  data () {
    return {
      isLoading: false,
      data: [],
      pagination: {},
      params: {
        page: 1,
        size: 20,
        keyword: null,
        role: null
      }
    }
  },
  computed: {
    roleEnum () {
      return roleEnum
    }
  },
  methods: {
    ...mapMutations(['SETGLOBALMASK']),
    // 
    getList () {
      this.isLoading = true
      return new Promise((resolve) => {
        checkAllUsers(this.params).then(response => {
          setTimeout(() => {
            const { result, code } = response.data
            if (code === 0) {
              const { list, total } = result
              this.data = list
              this.pagination.total = total
              this.pagination.page = this.params.page
              this.pagination.size = this.params.size
              this.isLoading = false
              resolve()
            }
          }, this.$store.state.apiDelay)
        })
      })
    },
    // 
    deleteUser (id) {
      this.SETGLOBALMASK(true)
      deleteUser({ id })
        .then((response) => {
          setTimeout(() => {
            const { code, msg } = response.data
            if (code === 0) {
              this.SETGLOBALMASK(false)
              this.$message.success(msg)
              this.getList()
            }
          }, this.$store.state.apiDelay)
        })
    },
    //  size 
    handleSizeChange (val) {
      this.params.size = val
      /**
       *  size  page 
       *  page 
       *  page 
       *
       *  ElementUI  size  page  size  page 
       *  this.params.page this.params.size 
       */
      if (
        this.pagination.total &&
        Math.ceil(this.pagination.total / val) >= this.params.page
      ) {
        this.getList()
      }
    },
    //  page 
    handleCurrentChange (val) {
      this.params.page = val
      this.getList()
    },
    // 
    handleCreateUser () {
      this.$router.push({
        path: '/permission-operation',
        query: {
          mode: 'create'
        }
      })
    },
    // 
    handleEdit (id) {
      this.$router.push({
        path: '/permission-operation',
        query: {
          mode: 'eidt',
          id
        }
      })
    },
    // 
    handleDelete (id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.deleteUser(id)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    // 
    handleSearch () {
      this.getList()
    },
    //  input  params.role
    handleInput () {
      this.params.role = null
    },
    // role 
    selectHandleChange () {
      this.params.keyword = null
      this.getList()
    }
  },
  created () {
    // 
    this.getList()
  },
  mounted () {},
  watch: {}
}
</script>

<style lang="scss" scoped>
.list-wrapper {
  width: 100%;
  height: 100%;
  .space {
    flex: 1;
  }
}
</style>
